/*
Note: any .css or .scss files included in the 'styles' directory
will be correctly compiled during `gulp serve` and `gulp`
 */
$nav_border: 1px solid #eee;
body,
html {
  font-family: 'Roboto', 'Helvetica', sans-serif;
  margin: 0;
  padding: 0;
}
.container {
  width: 1200px;
  margin: auto;
  min-height: 600px;
}
.container-nav {
  width: 800px;
  h1 {
    position: absolute;
    top: 245px;
    text-align: center;
    width: 800px;
    font-size: 42px;
    color: #669900;
  }
}
.nav {
  position: static;
  a {
    text-align: center;
    display: block;
    line-height: 1em;
    font-size: 1.3em;
    background-color: #ccc;
    border-left: $nav_border;
    color: #737373;
    text-decoration: none;
    padding: 8px 0;
  }
  .first-nav {
    border-top-left-radius: 1em;
    border-bottom-left-radius: 1em;
    border-left: $nav_border;
  }
  .last-nav {
    border-top-right-radius: 1em;
    border-bottom-right-radius: 1em;
    border-right: $nav_border;
  }
}
.foot {
  border-top: 1px solid #ccc;
  .container {
    min-height: 60px;
    height: 60px;
  }
  p {
    text-align: center;
    font-size: 1em;
  }
}
.content {
  background-position: center;
  background-repeat: no-repeat;
}
.content1 {
  background-image: url('../images/1.jpg');
  height: 380px;
}
.content2 {
  background-image: url('../images/2.png');
  height: 330px;
  text-align: center;
  .container {
    width: 1100px;
    h5 {
      color: #fff;
      margin-top: 135px;
      font-size: 56px;
    }
  }
}
.content3 {
  background-image: url('../images/3.jpg');
  height: 680px;
  ul {
    color: #98b34d;
    padding-left: 80px;
    margin-top: 120px;
    li {
      padding-left: 16px;
      font-size: 18px;
      font-weight: 600;
      margin-top: 8px;
      margin-bottom: 8px;
      line-height: 26px;
    }
  }
}
.content4 {
  background-image: url('../images/4.jpg');
  height: 680px;
  h4 {
    text-align: center;
    font-size: 28px;
    margin: 26px auto;
    color: #898989;
  }
}
.content5 {
  height: 80px;
  .container {
    height: 80px;
    min-height: 60px;
  }
}
